let pageAgur = ''
export default {
    onLoad(option){
        if(option) {
            pageAgur = option
            this.getthtypeShop(pageAgur)
        }
        console.log(option)
       
    },
        
    data(){
        return {
            orgoods:'',
            type:[]
        }
    },

    methods: {
        getthtypeShop(data){
            this.$http.getthtypeShop(data).then(res=>{
                if(res.status == 200) {
                    this.orgoods = res.data.orgoods
                    this.type = res.data.type
                }
            })
        }
    },
}
